export default {
	data() {
		return {
			photoList: [],
			chapter_id: 0,
			book_id: 0,
			max_chapter_num: 0,
			min_chapter_num: 0,
			isShow: true
		};
	},
	components: {},
	onLoad(option) {
		this.chapter_id = option.id;
		this.book_id = option.book_id;
		this.max_chapter_num = option.max_chapter_num;
		this.min_chapter_num = option.min_chapter_num;
		uni.request({
			url: 'http://192.168.2.100/showPhotolist/' + option.id, //仅为示例，并非真实接口地址。
			data: {},
			header: {},
			success: res => {
				this.photoList = res.data;
			}
		});

		//这里表示当进入页面的时候就开始执行下拉刷新动
	},
	methods: {
		getChapter(i) {
			if (i == 'mix') {
				--this.chapter_id;
			} else {
				++this.chapter_id;
			}
			if (this.chapter_id > this.max_chapter_num) {
				uni.showToast({
					title: '没有更多'
				});
				this.min_chapter_num++;
			} else if (this.chapter_id < this.min_chapter_num) {
				uni.showToast({
					title: '没有更多'
				});
				this.min_chapter_num--;
			} else {
				uni.request({
					url: 'http://192.168.2.100/showPhotolist/' + this.chapter_id, //仅为示例，并非真实接口地址。
					data: {},
					header: {},
					success: res => {
						this.photoList = res.data;
					}
				});
			}
		},
		backTobook() {
			uni.navigateTo({
				url: '../chapter/index?id=' + this.book_id
			});
		},
		getData() {
			this.chapter_id--;
			// uni.navigateTo({
			// 	url:'index?id=' + this.chapter_id,
			// })
			uni.request({
				url: 'http://192.168.2.100/showPhotolist/' + this.chapter_id, //仅为示例，并非真实接口地址。
				data: {},
				header: {},
				success: res => {
					this.photoList = res.data;
				}
			});
			uni.stopPullDownRefresh();
		},
		setShow() {
			if (this.isShow) {
				this.isShow = false;
			} else {
				this.isShow=true
			}
		}
	},
	onReachBottom() {
		// this.chapter_id++;
		// uni.request({
		// 	url: 'http://192.168.2.100/showPhotolist/' + this.chapter_id, //仅为示例，并非真实接口地址。
		// 	data: {},
		// 	header: {},
		// 	success: res => {
		// 		for (var i = 0; i < res.data.length; i++) {
		// 			this.photoList.push(res.data[i]);
		// 		}
		// 	}
		// });
		// this.loadMore.status = 'loading';
	},
	onPullDownRefresh() {
		// this.chapter_id--;
		// uni.navigateTo({
		// 	url:'index?id=' + this.chapter_id,
		// })
		uni.request({
			url: 'http://192.168.2.100/showPhotolist/' + this.chapter_id, //仅为示例，并非真实接口地址。
			data: {},
			header: {},
			success: res => {
				this.photoList = res.data;
			}
		});
		uni.stopPullDownRefresh();
	}
};
